<template>
  <div class="container">
    <!-- 头部导航 -->
    <navbar class="topnav">
      <span class="iconfont iconjiantou2" slot="back" @click="forback"></span>
      <h4 slot="title" class="title">栏目管理</h4>
    </navbar>
    <!-- 上部分 -->
    <div class="top">
      <p class="tips">点击删除以下频道</p>
      <div class="box">
        <span
          v-for="(item,index) in arrUp"
          :key="index"
          :class="{disable:dis.includes(item.name)}"
          @click="delcloumn(item,index)"
        >{{item.name}}</span>
      </div>
    </div>
    <!-- 下部分 -->
    <div class="bottom">
      <p class="tips">点击添加以上频道</p>
      <div class="box">
        <span
          v-for="(item,index) in arrDown"
          :key="index"
          @click="addcloumn(item,index)"
        >{{item.name}}</span>
      </div>
    </div>
  </div>
</template>

<script>
import navbar from "@/components/navbar.vue";
export default {
  // 组件创建时，获取本地的栏目数据
  mounted() {
    const cloumn = JSON.parse(localStorage.getItem("column")) || [];
    // 筛选已经添加的栏目
    this.arrUp = cloumn.filter((item, index) => {
      return item.is_top == 1;
    });
    // 筛选没有经添加的栏目
    this.arrDown = cloumn.filter((item, index) => {
      return item.is_top == 0;
    });
  },
  // 当组件销毁时,保存修改的数据
  destroyed() {
    this.merge = [...this.arrUp, ...this.arrDown];
    localStorage.setItem("column", JSON.stringify(this.merge));
    // 重新刷新数据
    // location.reload();
  },
  data() {
    return {
      merge: [],
      // 禁止修改的栏目
      dis: ["关注", "头条"],
      // 上部分
      arrUp: [],
      // 下部分
      arrDown: []
    };
  },
  methods: {
    // 添加栏目(下)
    addcloumn(item, index) {
      this.arrDown[index].is_top = 1;
      this.arrDown.splice(index, 1);
      this.arrUp.push(item);
    },
    // 删除栏目
    delcloumn(item, index) {
      // 判断如果不是关注和头条这连个栏目则可以删除
      if (!this.dis.includes(item.name)) {
        this.arrUp[index].is_top = 0;
        this.arrUp.splice(index, 1);
        this.arrDown.push(item);
      }
    },
    forback() {
      this.$router.push("/index");
    }
  },
  components: {
    navbar
  }
};
</script>

